
// header
.header--sticky {
	.nav {
		position: fixed;
		width: 100%;
		top: -10px;
		border-top: none;
		z-index: 999;
		box-shadow: 0 2px 5px rgba(0,0,0,.3);
		// animate
		@include animation(bounceInDown 1.5s);
        @include max-responsive(1100px) {
        	width: 1100px;
        }
	}
	.nav--sticky {
		//height: 56px;
		padding-top: 10px;
        
        
             
        
	}
	.nav .container {
		width: 100%;
		padding-left: 200px;
		// padding-right: 60px;
        @include min-responsive (1600px) {
        	padding-right: 100px;
        }
        
        
	}
    
    .nav .social--container {
        padding-left: 0;
        
    }
    .social--fixed .social--text {
        padding: 11px 0 0 25px;
    }
	// .nav__sub {
	// 	top: 46px;
	// }
	.nav__link {
		padding: 14px 5px;
		// @include transition(none);
        @include range-responsive (1180px, 1400px) {
            padding: 14px 7px;
        }
        @include min-responsive(1400px) {
        	padding: 14px 12px;
        }
	}
	.logo--sticky {
		display: inline-block;
		position: absolute;
		left: 25px;
		top: 12px;
		img {
			height: 20px;
		}
	}
}