.article {
	// position: relative;
	&__wrap {
		&__grid {
			position: relative;
			&--flex {
				display: block;
				@include display-flex();
				flex-wrap: wrap;
				-webkit-flex-flow: wrap;
				.article__grid {
					margin-bottom: 20px;
				}
			}
			&--fixed {
				// display: block;
				& > div {
					height: 290px;
				}
				// .article__grid {
				// 	height: 100%;
				// 	border-bottom: 1px solid #dfdfdf;
				// }
				// .article__box {
				// 	padding: 10px 10px 0 10px;
				// }
				.article__lead {
					@include title_overflow(auto, 85px);
				}
				.article__title {
					@include title_overflow(65px, 65px);
					// margin: 0;
				}
			}
			&--vik {
				& > div {
					height: 325px;
				}
				.article__grid {
					height: 100%;
					border-bottom: 1px solid #dfdfdf;
				}
				.article__lead {
					@include title_overflow(auto, 105px);
					margin: 0;
				}
				.article__title {
					@include title_overflow(auto, 45px);
				}
			}
		}
	}
	&__boxsubtitle {
		padding: 5px 10px;
		background: #fff;
		position: absolute;
		left: 0;
		top: 15px;
		z-index: 1;
		color: $color-kcm-orange;
	}
	&__subtitle {
		text-transform: uppercase;
		@include font-pack(false, 12px, 400, 1, false);
		&--inline {
			padding: 0 5px 0 0;
			display: inline-block;
			@include font-pack(false, false, false, false, $color-kcm-orange);
		}
		&--kompasid {
			margin: 0 0 10px;
			@include font-pack(false, 14px, 700, false, $color-kcm-blue);
		}
		a:hover {
			color: #000;
		}
	}
	&__asset {
		position: relative;
		// min-height: 117px;
		img {
			display: block;
			width: 100%;
		}
		&--kompasid {
			margin-bottom: 10px;
			height: 150px;
			overflow: hidden;
			// img {
			// 	max-width: none;
			// 	width: auto;
			// 	height: 100%;
			// }
		}
		&--god {
			width: 100%;
			height: 163px;
			background-position: center center;
			background-size: cover;
		}
	}
	&__title {
		@include font-pack(false, 18px, 300, 1.2, #000);
		margin: 0 0 10px;
		&--big {
			@include font-pack(false, 32px, false, 1.3, false);
		}
		&--medium {
			@include font-pack(false, 26px, false, 1.3, false);
		}
		&--small {
			@include font-pack(false, 18px, false, 1.3, false);
		}
		&--bullet {
			position: relative;
			padding: 7px 20px 0 30px;
			@include font-pack(false, 16px, false, 1.3, false);
			height: 77px;
			overflow: hidden;
			margin: 0;
			border-bottom: 1px solid #fff;
			&:after {
				content: '';
				position: absolute;
				top: 12px;
				left: 10px;
				width: 8px;
				height: 8px;
				background: #2a2a2a;
				// border-radius: 0;
			}
		}
		// &--opinion {
		// 	@include font-pack(false, 26px, false, 1.3, false);
		// 	margin: 0 0 5px;
		// }
		&--kompasid {
			height: 44px;
			overflow: hidden;
			// padding: 0 0 10px;
		}
	}
	&__box {
		position: relative;
		margin: -20px 10px 0;
		background: #fff;
		padding: 10px;
		// &--small {
		// 	margin: -40px 15px 0;
		// 	padding: 15px;
		// }
		&--medium {
			margin: -40px 15px 0;
			padding: 15px;
		}
		&--big {
			margin: -60px 30px 0;
			padding: 10px 20px;
		}
		&--offset-0 {
			margin: 0;
			padding: 10px 0;
		}
		&--topic {
			width: 100%;
			height: 150px;
			padding: 35px 20px;
			background: #9b9b9b;
			b {
				font-weight: 300;
				color: #fff;
				font-size: 28px;
				overflow: hidden;
				display: block;
				overflow: hidden;
				max-height: 75px;
			}
		}
	}
	&__grid {
		position: relative;
	}
	&__list {
		position: relative;
		margin: 0 0 15px;
		background: #fff;
		&:after {
			content: '';
			position: absolute;
			border-bottom: 1px solid #dfdfdf;
			width: 100%;
			bottom: 0;
			z-index: 1;
		}
		&__asset {
			width: 197px;
			float: left;
			padding-right: 20px;
			background: #fff;
			z-index: 2;
			position: relative;
		}
		&__title {
			height: 70px;
			overflow: hidden;
			margin-bottom: 18px;
		}
		&__item {
			// border-bottom: 1px solid #9B9B9B;
			margin-bottom: 15px;
			// padding-bottom: 5px;
			&:last-child {
				// border-bottom: transparent;
				margin-bottom: 0;
			}
		}
		&__opinion {
			.article__list__title {
				margin-bottom: 0;
			}
			.opinion__author {
				margin-bottom: 2px;
			}
		}
	}
	&__date {
		@include font-pack(false, 12px, false, false, #666);
		margin: 0 0 10px;
		display: inline-block;
	}
	&__lead {
		@include font-pack(false, 15px, 300, 1.4, #808080);
		margin: 0 0 10px;
		&--kompasid {
			padding: 20px 0;
			position: relative;
			margin: 0;
			&:before {
				content: '';
				position: absolute;
				left: 0;
				top: 0;
				width: 20%;
				border: 1px solid #9b9b9b;
			}
		}
	}
	&__media {
		display: block;
		margin: 0 0 10px;
		span {
			@include font-pack(false, 14px, 400, 1, $color-kcm-orange);
			// padding: 5px;
			display: inline-block;
			// border: 1px solid #9b9b9b;
			// .icon {
			// 	margin-right: 7px;
			// 	vertical-align: top;
			// 	font-size: 12px;
			// }
		}
	}
	&__time {
		@include font-pack(false, 18px, 300, 1.5, #9B9B9B);
		margin-bottom: 10px;
		.icon {
			margin-right: 5px;
			font-size: 15px;
		}
		&--fixed {
			position: absolute;
			z-index: 1;
			bottom: 0;
			left: 0;
			margin: 0;
			background: rgba(0,0,0,.7);
			padding: 5px 5px 5px 25px;
			@include font-pack(false, 12px, 400, false, #fff);
			&:before {
				content: '';
				position: absolute;
				width: 15px;
				height: 15px;
				border-radius: 20px;
				// border: 1px solid #fff;
				background: $color-kcm-blue;
				left: 6px;
				bottom: 6px;
			}
			&:after {
				content: '';
				position: absolute;
				top: 11px;
				left: 11px;
				// -webkit-transform: translate(-40%, -50%);
				// transform: translate(-40%, -50%);
				border-style: solid;
				border-width: 3px 0 3px 6px;
				border-color: transparent transparent transparent #fff;
			}
		}
	}
	&__photo {
		&__wrap {
			// padding-top: 15px;
			.icon-photo-slider {
				position: absolute;
				color: #fff;
				z-index: 1;
				font-size: 50px;
				line-height: 1;
				width: 30px;
				text-align: center;
				top: 33%;
			}
			.icon-photo-left {
				left: 20px;
			}
			.icon-photo-right {
				right: 20px;
			}
		}
		&__count {
			@include font-pack(false, 18px, 700, false, $color-kcm-orange-light);
			// margin-bottom: 20px;
		}
	}
	&__link {
		display: block;
	}
	// override
	&__list {

		// list break : block video
		&--tv {
			margin: 10px 0 40px;
		}
		&--video {
			.article__list__asset {
				width: 550px;
			}
			.article__list__title {
				height: 222px;
			}
			.article__title {
				@include font-pack(false, 24px);
			}
		}
	}
	&__partner {
		// position: absolute;
		// bottom: 6px;
		// left: 0;
		// width: 100%;
		// text-align: right;
		height: 50px;
		padding: 0 0 5px;
	}
	&__footnote {
		padding: 8px 0 0;
		border-top: 1px solid #9b9b9b; 
		@include font-pack(false, 12px, false, false, false);
	}
}



