.headline {
	position: relative;
	background: #000;
	color: #fff;
	// a:hover {
	// 	color: inherit;
	// }
	&__subtitle {
		position: absolute;
		left: 0;
		top: 30px;
		z-index: 1;
		background: #fff;
		text-transform: uppercase;
		padding: 10px;
		@include font-pack(false, 12px, 700, false, $color-kcm-blue-light);
	}
	&__big {
		position: relative;
		overflow: hidden;
		// width: 100%;
		height: 493px;
		&__row {
			position: relative;
		}
		&__item {
			display: block;
		}
		&__link {
			display: block;
			&:hover {
				color: #fff;
			}
		}
		&__img {
			width: 740px;
			// height: 375px;
			height: 493px;
			overflow: hidden;
			position: relative;
		}
		&__box {
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			padding: 20px 0;
			background: rgba(0,0,0,.7);
		}
		&__title {
			@include font-pack(false, 40px, 300, 1.1, false);
			padding: 0 20px;
			margin: 0 0 10px;
			// @include title_overflow(auto, 82px);
		}
		&__date {
			padding: 0 20px;
			@include font-pack(false, 12px, 600, false, #9B9B9B);
		}
	}
	&__thumb {
		position: relative;
		overflow: hidden;
		// width: 100%;
		height: 218px;
		&__row {
			position: relative;
			// margin: 0 -10px;
			margin: 0;
			overflow: hidden;
		}
		&__item {
			padding: 0;
			// width: 192.4444px!important;
			width: 190px;
			outline: 0;
			&.slick-current .headline__thumb__img:after {
				content: '';
				position: absolute;
				z-index: 1;
				top: 0;
				left: 0;
				width: 100%;
				border-top: 4px solid $color-kcm-blue;
			}
			// &:first-child .headline__thumb__title {
			// 	padding: 0 0 0 6px;
			// }
			// &:last-child .headline__thumb__title {
			// 	padding: 0 6px 0 0;
			// }
		}
		&__img {
			margin: 0 0 5px;
			position: relative;
			cursor: pointer;
		}
		&__title {
			@include font-pack(false, 14px, 400, false, false);
			@include title_overflow(73px, 73px);
			padding: 0 6px;
		}
		&__link {
			display: block;
			&:hover {
				color: lighten($color-kcm-blue, 20%);
			}
		}
	}
}